@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  font-family: "Nunito Sans", sans-serif;
}

html {
  box-sizing: border-box;
  max-width: 1440px;
}

*, *::after*::before {
  box-sizing: inherit;
}

body {
  background-color: #F8F8F8;
  max-width: 1120px;
  margin: auto;
}

@media (min-width: 1440px) and (max-width: 6000px) {
  body {
    background-color: #F8F8F8;
    max-width: 1120px;
  }
}
/* 1 часть Лого и виджеты */
.header {
  background-color: white;
  border-bottom: 1px solid #D9FFF5;
  min-height: 87px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 16px;
}

@media (min-width: 768px) and (max-width: 1119px) {
  .header {
    padding-left: 30px;
    padding-right: 40px;
  }
}
@media (min-width: 1120px) and (max-width: 4000px) {
  .header {
    float: right;
    display: flex;
    width: 420px;
    align-items: center;
    padding: 30px 0 0px;
    height: 60px;
    background-color: #F8F8F8;
    border-bottom: none;
  }
}
/* Переделанный хедер */
.navbar {
  background-image: url(./static/navbar.svg);
}

@media (min-width: 768px) and (max-width: 1119px) {
  .logo {
    padding-left: 15px;
  }
}
@media (min-width: 768px) and (max-width: 1119px) {
  .divider, .logo {
    background-image: url(./static/divider.svg);
    background-repeat: no-repeat;
    background-position: left;
    min-width: 2px;
    min-height: 32px;
  }
}
.divider + .logo {
  display: none;
}

.divider.dividerSmall {
  background-image: url(./static/divider.svg);
  background-repeat: no-repeat;
  background-position: left;
  min-width: 2px;
  min-height: 32px;
  display: block;
}

@media (min-width: 1120px) and (max-width: 4000px) {
  .navbar {
    display: none;
  }
}
.header__line--disappear {
  display: none;
}

@media (min-width: 768px) and (max-width: 1119px) {
  .header__line--disappear {
    display: block;
  }
}
.buttonCirlce {
  height: 40px;
  width: 40px;
  background-color: #FF3E79;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}

.buttonCirlce__phone {
  background-image: url(./static/phone.svg);
  border: 0;
}

.phone--disappear {
  display: none;
}

@media (min-width: 768px) and (max-width: 1119px) {
  .phone--disappear {
    display: block;
  }
}
.buttonCirlce__message {
  background-image: url(./static/message1.svg);
  border: 0;
}

.message--disappear {
  display: none;
}

@media (min-width: 768px) and (max-width: 1119px) {
  .message--disappear {
    display: block;
  }
}
.buttonCirlce__office {
  background-image: url(./static/office.svg);
  border: 0;
}

.office--disappear {
  display: none;
}

@media (min-width: 768px) and (max-width: 1119px) {
  .office--disappear {
    display: block;
  }
}
.buttonCirlce__settings {
  background-image: url(./static/settings.svg);
  border: 0;
}

.text__settings {
  display: none;
}

.buttonCirlce__search {
  background-image: url(./static/search.svg);
  border: 0;
}

.text__search {
  display: none;
}

/* 2 часть Заголовок и скролл */
.heading {
  min-height: 40px;
  display: flex;
  justify-content: space-between;
  padding: 24px 0 20px;
}

@media (min-width: 1120px) and (max-width: 4000px) {
  .heading {
    padding: 40px 0 20px;
  }
}
.main {
  background-color: #F8F8F8;
}

.content-name {
  font-family: "Nunito Sans", sans-serif;
  font-size: 30px;
  width: 350px;
  padding-left: 15px;
  letter-spacing: -1px;
  font-weight: bold;
}

@media (min-width: 768px) and (max-width: 1119px) {
  .content-name {
    padding-left: 20px;
  }
}
@media (min-width: 1120px) and (max-width: 4000px) {
  .content-name {
    width: 650px;
  }
}
.img {
  height: 45px;
}

.scroller {
  display: flex;
  justify-content: center;
  overflow-x: auto;
}

.gradient {
  position: absolute;
  top: 10;
  right: 0;
  height: 45px;
  width: 20px;
  background: rgb(255, 255, 255);
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(248, 248, 248, 0.9388130252) 70%);
}

.link {
  display: inline-block;
  height: 20px;
  color: #7E7E82;
  font-size: 17px;
  padding: 10px 15px 10px 15px;
  text-decoration: none;
  font-weight: 600;
}

@media (min-width: 1120px) and (max-width: 4000px) {
  .link {
    padding: 10px 20px 10px 20px;
  }
}
.link:active {
  border: 2px solid #B8FFEC;
  border-radius: 6px;
  background-color: white;
}

.scroller__items {
  padding: 10px 10px;
  display: flex;
  justify-content: start;
  align-items: stretch;
  overflow-x: auto;
  list-style-type: none;
  white-space: nowrap;
  padding-left: 16px;
  z-index: 0.1;
}

@media (min-width: 1120px) and (max-width: 4000px) {
  .scroller__items {
    flex-wrap: wrap;
  }
}
.scroller__items::-webkit-scrollbar {
  display: none;
}

ul.scroller__items li.selected a {
  color: #7E7E82;
  border: 2px solid #B8FFEC;
  border-radius: 6px;
  background-color: white;
}

ul.scroller__items li a:hover {
  color: #7E7E82;
}

.el1-link--mod {
  display: none;
}

/* 3 часть контент текст */
.first-paragraph {
  font-size: 16px;
  color: #1B1C21;
  height: 100px;
  padding-top: 5px;
  padding-left: 16px;
  padding-right: 16px;
  padding-bottom: 20px;
  text-align: left;
  font-weight: 600;
}

@media (min-width: 1120px) and (max-width: 4000px) {
  .first-paragraph {
    display: block;
    font-size: 17px;
    height: 65px;
  }
}
.second-paragraph {
  display: none;
}

@media (min-width: 1120px) and (max-width: 4000px) {
  .second-paragraph {
    display: block;
    height: 110px;
    font-size: 17px;
    padding: 0px 16px 0px 16px;
    font-weight: 600;
  }
}
.text-search {
  display: none;
}

@media (min-width: 1120px) and (max-width: 4000px) {
  .text-search {
    display: block;
    align-items: center;
    padding-top: 10px;
    float: left;
    height: 24px;
    font-size: 18px;
    padding-right: 20px;
  }
}
.text-settings {
  display: none;
}

@media (min-width: 1120px) and (max-width: 4000px) {
  .text-settings {
    display: block;
    align-items: center;
    padding-top: 10px;
    float: left;
    height: 24px;
    font-size: 18px;
    padding-right: 20px;
  }
}
.read-more {
  color: #1B1C21;
  font-size: 18px;
  font-weight: 700;
  background: #F8F8F8;
  display: block;
  border: 0;
  cursor: pointer;
  padding-top: 30px;
  padding-left: 36px;
}

@media (min-width: 1120px) and (max-width: 4000px) {
  .read-more {
    padding-top: 0;
  }
}
.arrow {
  height: 12px;
  padding-right: 20px;
}

.picture {
  display: flex;
  justify-content: center;
  padding-top: 20px;
}

@media (min-width: 1120px) and (max-width: 4000px) {
  .picture {
    padding-top: 0px;
  }
}
.banner {
  width: 20rem;
}

@media (min-width: 768px) and (max-width: 1119px) {
  .banner {
    width: 25rem;
  }
}
/* Часть 4 Ремонт различных брендов */
.brands {
  background-color: #F8F8F8;
}

.title__brands {
  font-size: 16px;
  font-weight: 400;
  height: 48px;
  width: 100%;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: -0.2px;
  color: #7E7E82;
  padding: 15px;
  border-top: 1px solid #D9FFF5;
  background: linear-gradient(#F8F8F8, #FFFFFF);
}

@media (min-width: 768px) and (max-width: 6000px) {
  .title__brands {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 0 0 24px;
    font-size: 16px;
    font-weight: 400;
    height: 56px;
    width: 100%;
    line-height: 24px;
    text-transform: uppercase;
    letter-spacing: -0.2px;
    color: #7E7E82;
    border-top: 1px solid #D9FFF5;
    background: linear-gradient(#F8F8F8, #FFFFFF);
  }
}
/* swipper */
.image-slider {
  overflow-x: hidden;
  z-index: 0;
}

.image-slider .moy_style {
  position: inherit;
  margin-top: 16px;
}

.image-slider .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  line-height: 20px;
  font-size: 14px;
  background-color: #B5B6BC;
}

.circleArrow {
  background-image: url(./static/arrowone.svg);
  background-repeat: no-repeat;
  background-position: 185px 15px;
}

.swiper-wrapper .swiper-slide {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  min-height: 72px;
  min-width: 240px;
  max-height: 72px;
  max-width: 240px;
  margin-top: 16px;
  background-color: white;
  border: 2px solid #EAEAEA;
  border-radius: 8px;
}

.image-slider__image {
  max-height: 39px;
  max-width: 116px;
  padding-left: 16px;
}

.apple > .image-slider__image {
  width: 18%;
  height: 44px;
  padding-bottom: 10px;
}

.hp > .image-slider__image {
  width: 18%;
  height: 52px;
}

.lenovo {
  margin-left: 16px;
}

@media (min-width: 768px) and (max-width: 6000px) {
  .lenovo {
    margin-left: 0px;
  }
}
.gradient__swiper {
  position: absolute;
  bottom: 35px;
  height: 75px;
  width: 15px;
  z-index: 1;
}

/* Убираем из виду второй блок(копия свипера), при экране менее 768 */
.brands__container {
  display: none;
}

/* показывает копию блока с свипером */
@media (min-width: 768px) and (max-width: 6009px) {
  .brands__container {
    display: block;
  }
}
/* Убираем из виду свипер блок, при экране более 768 */
@media (min-width: 768px) and (max-width: 6000px) {
  .image-slider {
    display: none;
  }
}
.brands__wrapper {
  display: flex;
  flex-wrap: wrap;
  padding-left: 24px;
  padding-right: 24px;
  grid-column-gap: 18px;
  grid-row-gap: 16px;
  margin-top: 24px;
  float: right;
}

.circleArrow {
  background-image: url(./static/arrowone.svg);
  background-repeat: no-repeat;
  background-position: 11em 1em;
}

.brands__card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  min-height: 72px;
  min-width: 224px;
  background-color: white;
  border: 2px solid #EAEAEA;
  border-radius: 8px;
}

@media (min-width: 1120px) and (max-width: 6000px) {
  .brands__card {
    min-width: 240px;
  }
}
.brands__img {
  max-height: 39px;
  max-width: 116px;
  padding-left: 16px;
}

@media (min-width: 768px) and (max-width: 1119px) {
  .lenovo {
    margin-left: 0;
  }
}
@media (min-width: 768px) and (max-width: 1119px) {
  .apple > .brands__img {
    width: 18%;
    height: 44px;
    padding-bottom: 10px;
  }
}
@media (min-width: 1120px) and (max-width: 6600px) {
  .apple > .brands__img {
    width: 18%;
    height: 44px;
    padding-bottom: 10px;
  }
}
@media (min-width: 768px) and (max-width: 1119px) {
  .hp > .brands__img {
    width: 18%;
    height: 52px;
  }
}
@media (min-width: 1120px) and (max-width: 6000px) {
  .hp > .brands__img {
    width: 18%;
    height: 52px;
  }
}
#dots {
  position: absolute;
}

.show {
  display: none;
}

@media (min-width: 768px) and (max-width: 6000px) {
  .show {
    display: block;
  }
}
.brands__wrapper, .more {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  grid-column-gap: 18px;
  grid-row-gap: 16px;
}

