.hidden {
  display: none;
}

@media (min-width: 1120px) and (max-width: 6000px) {
  .acer {
    display: flex;
  }
}
@media (min-width: 1120px) and (max-width: 6000px) {
  .sony {
    display: flex;
  }
}
.samsung2 {
  display: none;
}

.apple2 {
  display: none;
}

/* @media (min-width : 1120px) and (max-width: 6000px) {
    #more{
        display: block;
      }
  } */
#show-more {
  color: #1B1C21;
  font-size: 18px;
  font-weight: 700;
  height: 50px;
  width: 170px;
  border: 0;
  cursor: pointer;
  box-sizing: border-box;
  outline: none;
  background-color: #F8F8F8;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  touch-action: manipulation;
}

#show-more-img {
  height: 15px;
  width: 15px;
  padding: 0 0 0 35px;
  transform: rotateX(0);
}

/* Всплывающее меню */
/* шапка */
/* #checkbox_toggle:checked + .side-panel__navbar{
    display: none;
}
#checkbox_toggle:checked + .side-panel__navbar{
    display: block;
}
#checkbox_toggle:checked + .side-panel__navbar {
    left: 0;
} */
.checkbox {
  opacity: 0;
  height: 40px;
  width: 40px;
  cursor: pointer;
}

.checkbox:hover {
  cursor: pointer;
}

.side-panel__navbar {
  display: none;
}

.side__panel {
  overflow: auto;
}

.side-panel__navbar {
  display: block;
  position: fixed;
  z-index: 2;
  top: 0;
  left: -400px;
  background: white;
  transition: all 0.5s;
  width: 320px;
  height: 100vh;
  box-shadow: 30px 0 30px rgba(0, 0, 0, 0.4);
  color: #FFF;
  overflow-y: auto;
}

@media (min-width: 1440px) and (max-width: 6000px) {
  .side-panel__navbar {
    display: block;
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0px;
    background: white;
    transition: all 0.5s;
    width: 320px;
    height: 100vh;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0.4);
    color: #FFF;
  }
}
.side__panel__active {
  display: block;
  left: 0px;
}

.side_panel__header {
  display: flex;
  justify-content: space-around;
  padding-top: 20px;
  padding-bottom: 20px;
  align-items: center;
}

.closeModal {
  left: -400px;
}

.closeModal__active {
  display: block;
  position: fixed;
  top: 0;
  left: 320px;
  width: 100%;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.95);
  z-index: 2;
  transition: all 0.5s;
}

.side-button-exit {
  font-size: 30px;
  border-radius: 20px;
  border-style: solid;
  border-color: white;
  position: relative;
  z-index: 1;
  cursor: pointer;
  /* transform: rotate(45deg); */
  color: #BFE2FF;
  transition: all 280ms ease-in-out;
}

.side-button-exit:hover {
  transform: rotate(90deg) scale(1.1);
  color: #b42828;
}

.side-panel__exit {
  background-image: url(./static/exit.svg);
}

@media (min-width: 1440px) and (max-width: 6000px) {
  .side-panel__exit {
    display: none;
  }
}
.side-panel__logo {
  padding-right: 60px;
  background-image: none;
}

/* @media (min-width : 1440px) and (max-width: 6000px) {
    .logo{
       display: block;
    }
} */
@media (min-width: 768px) and (max-width: 6000px) {
  .side-panel__logo {
    padding-right: 60px;
  }
}
.side-panel__search {
  background-image: url(./static/search_nav.svg);
  transition: all 280ms ease-in-out;
}

.side-panel__search:hover {
  transform: rotate(10deg) scale(1.1);
  color: #b42828;
}

/* список */
.list__panel {
  font-size: 16px;
  z-index: 2000;
  list-style-type: none;
  overflow-y: auto;
  padding-bottom: 10vh;
}

.list__panel--scroll::-webkit-scrollbar {
  display: none;
}

.li__panel {
  padding-bottom: 20px;
}

.li__panel:hover {
  background-image: url(./static/highlight.svg);
  background-repeat: no-repeat;
  background-position: left;
}

.list__link {
  display: block;
  text-decoration: none;
  color: #1B1C21;
  font-weight: 600;
  padding-top: 10px;
  padding-left: 30px;
}

.list__link:hover {
  color: #7E7E82;
  cursor: pointer;
  font-weight: normal;
  background-image: url(./static/highlight.svg);
  background-repeat: no-repeat;
  background-position: left;
}

/* подвал сайдбара */
.nav__footer {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-around;
  padding: 20px;
  padding-right: 90px;
  height: auto;
  vertical-align: bottom;
}

.mail {
  color: #1B1C21;
  grid-column-start: 1;
  grid-column-end: 3;
  padding-top: 20px;
  font-size: 14px;
  font-weight: 600;
}

.number {
  color: #1B1C21;
  grid-column-start: 1;
  grid-column-end: 4;
  padding-top: 10px;
  font-size: 28px;
  font-weight: bold;
}

.nav__footer__link {
  cursor: pointer;
  text-decoration: none;
  color: #1B1C21;
  transition: all 180ms ease-in-out;
}

.nav__footer__link:hover {
  transform: rotate(0deg) scale(1);
  color: pink;
}

.language {
  text-decoration: none;
  color: #1B1C21;
  font-weight: 600;
  font-size: 18px;
  padding-top: 40px;
  cursor: pointer;
}

.language:hover {
  font-weight: 200;
}

/* CSS 768px Те теги которые не стилизовались ранее*/
@media (min-width: 768px) and (max-width: 1119px) {
  .logo__navbar {
    padding-right: 250px;
  }
  .main__heading {
    display: flex;
    justify-content: left;
  }
  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .about-service {
    padding: none;
  }
  .about-service__second-paragraph {
    display: block;
    height: 35px;
    overflow: hidden;
    padding: 10px 16px 5px 16px;
    font-size: 16px;
    font-weight: 600;
  }
}
/* CSS 1120px Те теги которые не стилизовались ранее*/
@media (min-width: 1120px) and (max-width: 4000px) {
  .logo {
    display: none;
  }
  .line {
    display: none;
  }
  .text__settings {
    display: block;
  }
  .text__search {
    display: block;
  }
  .el1-link {
    display: block;
  }
  .header__settings {
    padding: 10px;
  }
  .main__heading {
    float: left;
    display: flex;
    width: 700px;
    padding: 60px 0 0px;
    height: 60px;
  }
  .main__scroller {
    display: inline-block;
  }
  .el3 {
    display: none;
  }
  .el5 {
    display: block;
  }
  .content {
    display: flex;
    justify-content: center;
    align-items: top;
  }
  .picture__banner {
    border-radius: 5px;
    padding-bottom: 50px;
    padding-top: 0px;
    height: 20rem;
    width: 30rem;
  }
}